import { BrowserMultiFormatReader } from '@zxing/library';

export let cameraStream;
export const videoElement = document.getElementById("camera-preview");
export const cameraContainer = document.getElementById("camera-container");
export const inputCodigo = document.getElementById("codigo");

export let codeReader = new BrowserMultiFormatReader();

export function showSection(sectionId) {
    document.querySelectorAll('section').forEach(section => section.style.display = 'none');
    document.getElementById(sectionId).style.display = 'block';
}

export async function iniciarEscaneo() {
    try {
        cameraContainer.style.display = "block";
        const devices = await navigator.mediaDevices.enumerateDevices();
        const videoDevices = devices.filter(device => device.kind === "videoinput");

        if (videoDevices.length === 0) {
            alert("No se encontraron cmaras en el dispositivo.");
            cameraContainer.style.display = "none";
            return;
        }

        let selectedDeviceId;
        const backCamera = videoDevices.find(device => device.label.toLowerCase().includes("back"));
        if (backCamera) {
            selectedDeviceId = backCamera.deviceId;
        } else {
            selectedDeviceId = videoDevices[0].deviceId;
        }

        cameraStream = await navigator.mediaDevices.getUserMedia({
            video: { deviceId: selectedDeviceId }
        });

        if (cameraStream) {
            console.log("Cmara accesada exitosamente", cameraStream);
            videoElement.srcObject = cameraStream;
            videoElement.play();
            detectarCodigoDeBarras(selectedDeviceId);
        } else {
            throw new Error("No se pudo acceder al flujo de la cmara.");
        }
    } catch (error) {
        console.error("Error al iniciar el escaneo:", error);

        if (error instanceof DOMException) {
            console.error("Detalles del error:", error.message, error.name);
        }

        if (error.name === "NotAllowedError") {
            alert("El navegador necesita permisos para acceder a la cmara. Por favor, otrgales permisos.");
        } else if (error.name === "NotFoundError") {
            alert("No se encontraron cmaras disponibles.");
        } else if (error.name === "NotReadableError") {
            alert("La cmara est siendo utilizada por otra aplicacin.");
        } else if (error.name === "AbortError") {
            alert("El acceso a la cmara fue cancelado.");
        } else {
            alert("No se pudo acceder a la cmara. Verifica los permisos.");
        }

        cameraContainer.style.display = "none";
    }
}

export async function detectarCodigoDeBarras(deviceId) {
    try {
        const result = await codeReader.decodeOnceFromVideoDevice(deviceId, videoElement);
        if (result) {
            console.log("Cdigo detectado:", result.text);
            inputCodigo.value = result.text;
            emitirPitido();
            detenerEscaneo();
        }
    } catch (error) {
        console.error("Error al detectar el cdigo:", error);
        requestAnimationFrame(() => detectarCodigoDeBarras(deviceId)); // Sigue buscando
    }
}

export function detenerEscaneo() {
    if (cameraStream) {
        const tracks = cameraStream.getTracks();
        tracks.forEach(track => track.stop());
    }

    cameraContainer.style.display = "none";
    videoElement.pause();
    videoElement.srcObject = null;

    if (codeReader) {
        codeReader.reset();
    }
}

export function emitirPitido() {
    const audio = new Audio('https://example.com/beep.mp3');
    audio.play();
}
